.background{
    width: 430px;
    height: 520px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform:translate(-50%,-50%)
}
.background .shape{
    border-radius: 50%;
    width: 200px;
    height: 200px;
    position: absolute;
    
}
.shape:first-child{
    background: linear-gradient(#1845ad,#23a2f6);
    left: -80px;
    top: -80px;
}
.shape:last-child{
    background: linear-gradient( to right,#ff512f,#f09819);
    right: -30px;
    bottom: -80px;
}

.todo-form{
    min-height: 520px;
    width: 400px;
    background-color: rgba(255, 255, 255, 0.13);
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    border-radius: 10px;
    backdrop-filter: blur(10px);
    border: 2px solid rgba(255, 255, 255, 0.1);
    box-shadow: 0 0 40px rgba(255, 255, 255, 0.1);
    padding: 40px 35px;
}
.btn-add{
    background: linear-gradient( to right,#ff512f,#f09819);
    height: 40px;
}
.input{
    background-color: rgba(255, 255, 255, 0.1);
    min-height: 40px;
}

.line{
    background-color: rgba(255, 255, 255, 0.1);
}
.list{
    background-color: rgba(255, 255, 255, 0.1);
}
.list-done{
    background-color: rgb(0, 120,0);
}